@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: "Jost", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Jost", sans-serif;
}

.navbarItems {
  padding: 0.7rem 3rem 0.7rem;
}

.username-user {
  cursor: pointer;
}

.drop-down-logout {
  margin: 0.3rem 0.4rem;
}

.drop-down-logout:hover {
  cursor: pointer;
  background-color: #3645470f;
  padding: 0.1rem;
  border-radius: 0.2rem;
}

.desktop {
  display: grid;
  grid-template-columns: 2fr 1fr;
}

a {
  text-decoration: none;
}

.navbarLogo {
  margin: auto;
  color: #ffb037;
}

.nav-links {
  color: #364547;
  /* font-weight: bold; */
}

h1 {
  color: #364547;
}

.nav-menu {
  display: none;
}

.left-side-nav {
  justify-self: left;
}

.rightside-nav {
  justify-self: right;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  list-style: none;
  align-self: center;
}

.hamburger-menu {
  display: none;
  /* position: fixed; */
}

.line {
  width: 100%;
  height: 4px;
  background-color: #364547;
  transition: all 0.5s;
  border-radius: 1.5px;
}

.change.line-1 {
  transform: rotateZ(-405deg) translate(-3px, 4px);
}

.change.line-2 {
  opacity: 0;
}

.change.line-3 {
  transform: rotateZ(405deg) translate(-5px, -6px);
}

.dropdown {
  padding: 0 3rem;
  text-align: end;
  transition-duration: 4s;
  transition-delay: 2s;
}

.ddmenu {
  display: flex;
}

.drop {
  display: none;
}

@media screen and (max-width: 600px) {
  .navbarItems {
    padding: 0.7rem 1rem 0.7rem;
  }
  .rightside-nav {
    display: none;
  }
  .hamburger-menu {
    width: 20px;
    height: 20px;
    justify-self: end;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-self: center;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 30%;
    position: absolute;
    top: 4rem;
    left: -100%;
    opacity: 1;
    transition: all 0.7s ease;
  }
  .nav-menu.active {
    left: 25%;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    overflow: hidden;
    position: fixed;
    background-color: white;
    padding: 0;
    border-radius: 1rem;
    height: auto;
  }

  .nav-links {
    color: #364547;
    font-weight: 500;
    list-style: none;
    text-align: center;
    margin: 1rem 0 1rem;
  }
}

.hero-image {
