  background-size: cover;
  background-repeat: no-repeat;
  justify-content: center;
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgb(0 0 0 / 35%);
  object-fit: cover;
}

